@import url("https://fonts.googleapis.com/css2?family=Inter:wght@900&display=swap");
body {
  margin: 0;
  padding: 0;
  background: #222;
  color: #fff;
}

.main-container h1 {
  position: relative;
  background-color: #222;
  width: 100%;

  overflow: hidden;
  text-align: center;
  font-size: 40px;
  color: #fff;
  font-family: var(--font-inter);
}
.input-box {
  border: 4px #fff solid;
  border-radius: 10px;
  width: 300px;
  height: 25px;
  display: flex;
    justify-content: space-between;
    align-items: center;
}

.add-button {
  background: #222;
  border: 4px;

  margin-left: 10px;
  width: 50px;
  height: 35px;
   background: none;
    border: none;
    cursor: pointer;
    outline: none;
}


.todo-list {
  list-style: none;
  font-size: 20px;
  font-weight: 100;
  font-family: monospace;
  margin-top: 50px;
}
.remove-button {
  background: #222;
  border: 2px;
  margin-left: 10px;

  height: 25px;
}
#dustbin {
  max-width: 20px;
  max-height: 20px;
}
#plus {
  max-width: 24px;
  max-height: 24px;
}

.todo-item {
  margin-bottom: 20px;
  text-align: center;
  animation: deleteAnimation 0.5s ease-in-out forwards;
  text-transform: capitalize;
}
@keyframes anim {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

.input-container {
  text-align: center;
  display: flex;
    justify-content: center; 
    align-items: center;
}
@keyframes deleteAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
